import React, { Component } from 'react';

class Home extends Component {

  render() {
    return (
      <div className='homePageContainer'>

        <h4 className='homePageHeader'>Hey there, welcome to my personal website!</h4>
        <p className='homePageParagraph'>My name is Nick. I'm a student at the University at Buffalo pursuing a bachelors degree
          in computer science and engineering. If you'd like to learn more about me, please feel free to navigate through the site
        using the tabs above. For more information you can contact me through the social links below.</p>

      </div>
    )
  }
}

export default Home;



